// wxcharts插件有bug，小程序无法组件引入	

import wxCharts from '@/wxcomponents/wx-charts/wxcharts.js';

var canvaColumn = null;
export default {
	props: {
		// chartsData: [Object,null]
	},

	data() {
		return {
			cWidth: uni.upx2px(750),
			cHeight: uni.upx2px(500),
			pixelRatio: 1,
			cavId: 'cavPie',
			chartsData: {
				series: [
					{ name: '一班', data: 50, color: 'rgb(83, 175, 255)' },
					{ name: '二班', data: 30 },
					{ name: '三班', data: 20 },
					{ name: '四班', data: 18 },
					{ name: '五班', data: 8 },
					{ name: '六班', data: 8 },
					{ name: '七班', data: 8 },
					{ name: '八班', data: 8 },
					{ name: '九班', data: 8 },
					{ name: 'Ⅹ班', data: 8 }
				]
			}
		};
	},

	methods: {
		showPie() {
			const charts = new wxCharts({
				canvasId: this.cavId,
				type: 'pie',
				fontSize: 11,
				legend: true,
				background: '#ffffff',
				pixelRatio: this.pixelRatio,
				series: this.chartsData.series,
				animation: true,
				width: this.cWidth * this.pixelRatio,
				height: this.cHeight * this.pixelRatio,
				dataLabel: true
			});

			charts.addEventListener('renderComplete', () => {
				console.log('renderComplete');
			});
		},

// 		changeData() {
// 			//这里只做了柱状图数据动态更新，其他图表同理。
// 			canvaColumn.updateData({
// 				series: Data.ColumnB.series,
// 				categories: Data.ColumnB.categories
// 			});
// 		},
	},

	watch: {
		chartsData(val) {
			// console.log(this.chartsData);
			// this.showPie();
		}
	},

	created() {











	},

	mounted() {
		this.showPie();
	}
};
